@import (less) "common.less";

@jungleGreen: #2ABB9B;

@mainTheme: @greensea;
@buttonGreen: @jungleGreen;
@buttonHover: @patronus;
@crimson-red: #D24D57;
@danger: @alizarin;
@important: @pomegranade;
@active: @wetasphalt;
@unimportant: @asbestos;
@neutral: @clouds;
@grey: @normal-grey;
@warning: #E29C45;



.admin-menu{
  background: white;
  a{
    text-decoration: none;
    border: none;
    font-size: 18px;
    line-height: 60px;
    padding: 18px 15px;
    color: @missnorris;
    font-weight: 600;
    margin: 0 -2px;
    &.user{
      float: right;
      padding: 0 15px;
      line-height: 57px;
      &:hover{
        padding-bottom: 0;
      }
    }
    &:hover, &.current{
      color: @patronus;
      padding-bottom: 16px;
      border-bottom: 3px solid @patronus;
    }
  }
}

.multiselect-wrap{
  margin:0 !important;
  .multiselect{
    background: @white;
    border: 1px solid #e3e3e3;
    border-bottom: none;
    padding: 10px;
    margin: 0;
    ul{
      padding:0;
    }
    li{
      list-style: none;
      display: inline-block;
      padding: 6px;
      color: @white;
      margin: 2px 0;
      background: @patronus;
      border-radius: 7px;
    }
    span.fa{
      font-size: 13px;
      margin-left: 5px;
      position: relative;
      float: right;
      top: -3px;
      cursor: pointer;
    }
  }
  input{
    width: 100%;
    margin: 0;
    display: block;
    top: -4px;
    position: relative;
    border: 1px solid #e3e3e3;
    &:focus{
      outline: none;
    }
    &.full{
      border-top: none;
    }
  }
}
.knockout-autocomplete{
  &.menu {
    background-color: @white;
    padding: 3px 0;
    height: auto;
    width: 44%;
    li {
      text-transform: inherit;
      font-family: 'Roboto Regular';
      font-size: 15px;
      line-height: 18px;
      cursor: pointer;
      list-style: none;
      margin: 0px 0;
      padding: 3px 12px;
      display: block;
      background: @white;
      color: @black;
      &.selected{
        background: @patronus;
        color: @white;
      }
    }
  }
  &.floating-menu {
    position: fixed;
    border: thin #aaa solid;
    margin: 0;
    margin-top: 3px;
    -webkit-box-shadow: 1px 1px 4px rgba(50, 50, 50, 0.75);
    -moz-box-shadow:    1px 1px 4px rgba(50, 50, 50, 0.75);
    box-shadow:         1px 1px 4px rgba(50, 50, 50, 0.75);
  }
  &.after-menu {
    position: absolute;
  }
}

.progressBar {
  position: relative;
  width: 100%;
  height: 13px;
  margin: 20px auto;
  border: 1px solid @akcio;
  border-radius: 3px;
  overflow: hidden;
}

.progressBar div {
  position: relative;
  height: 100%;
  background-color: @patronus;
}

.progressBar .progressText {
  color: @white;
  position: absolute;
  margin: 0 auto;
  top: -3px;
  font-size: 12px;
  left: 0;
  right: 0;
  background-color: transparent;
}

.a-style-none{
  a, a:hover{
    margin:0;
    padding:0;
    color: @black;
    text-decoration: none;
    border-bottom: 0;
  }
}

h3{
  color: @filch;
}

input{
  background: @white;
  padding: 0 5px;
  height: 30px;
  font-size: 15px;
  border:1px solid #d3d3d3;
}
.inline{
  display: inline-block;
}
.block{
  display: block;
}

.items-head, .layer-head{
  margin: 15px 0;
  label.adder{
    float: right;
    line-height: 22px;
  }
  h1{
    display: inline;
  }
  h3{
    color: @filch;
  }
}
.items-body, .layer-body{
  margin: 20px 0;
}

.items{
  &.disciplines{
    .item .tags{
      text-align: right;
      float:right;
      min-width: 80px;
    }
  }
}
.item{
  background: @patronus-shade;
  color: @filch;
  font-size: 16px;
  padding: 9px;
  cursor: pointer;
  margin: 1px 0;
  &:hover, &.current{
    background: @patronus;
    color: @white;
    span.tag{
      color: @white;
    }
  }
  &.no-hover{
    &:hover{
      background: @patronus-shade;
      color: @filch;
    }
  }
  span.tag{
    color: @patronus-shade;
    margin: 0 8px;
    font-size: 23px;
  }
}

.items{
  .result{
    .a-style-none();
    a{
      display: block;
      width: 100%;
      border:1px solid @akcio;
      &:hover{
        border: 1px solid @patronus;
      }
    }
  }
}

.details-column.tile-boredom{
  background: @patronus-shade;
  width: 150px;
  height: 100px;
  padding: 5px 15px;
  span{
    display: block;
    font-size: 15px;
    color: @akcio;
    line-height: 15px;
    margin: 7px;
    font-weight: bold;
    cursor: pointer;
    &:hover{
      color: @filch;
    }
  }
}

.item.plan{
  input{
    width: 240px;
    height: 38px;
  }
}

.details-column{
  margin: 1%;
  input[type=text], input[type=password], select, textarea{
    width: 100%;
    &.time{
      width: 50px;
      text-align:center;
    }
    &.attempts{
      width: 130px;
      text-align: center;
    }
  }
}

.details-row.float-buttons{
  min-height: 60px;
  button{
    float:right;
    margin: 0 1% 0 0;
  }
}
.details-row.variants{
  input{
    width: 93%;
    height: 40px;
  }
  button{
    float: right;
  }
}
.details-row.theme{
  input{
    width: 87%;
    height: 40px;
  }
  span{
    min-width:100px;
    width:15%;
  }
}
.details-row.date{
  label{
    float: right;
    position: relative;
    top: -20px;
    right: 5px;
    color: @akcio;
    font-size: 12px;
  }
}
.details-row.special-item{
  border-top: 1px solid @akcio;
  border-bottom: 1px solid @akcio;
  margin-top: -1px;
  cursor: pointer;
  font-size: 16px;
  &:last-child{
    margin-bottom: -1px;
  }
  &:hover, &.current{
    background: @patronus;
    color: @white;
    span.tagged{color: @white;}
  }
  span.tagged{
    color: @patronus;
    float: right;
  }
}
.details-row.special-item-details{
  background: @patronus-shade;
  color: @filch;
  span.icon{
    font-size: 25px;
    margin: 0 20px 0 0;
    float: left;
    padding: 0px;
    width: 20px;
  }
  span.text{
    white-space: pre-wrap;
    font-size: 13px;
    display: table;
    text-align: justify;
  }
  .result-setter input{
    max-width: 83px;
  }
}

.stripe-table{
  width: 100%;
  padding: 15px 0;
  border-collapse: collapse;
  margin: 10px 0;
  th{
    padding: 10px;
  }
  tbody tr{
    &:nth-child(odd){
      background: @patronus-shade;
    }
    &:nth-child(even){
      background: @silver;
    }
  }
  td{
    padding: 5px;
  }
  button.remove{
    background: @crimson-red;
  }
}
table.paramset{
  button.remove{
    margin-right: 10px;
  }
  td{
    width: 40%;
    white-space: pre-wrap;
    &:first-child{
      width: 5%;
      text-align: center;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
}
table.themes{
  td{
    width: 80%;
    &:first-child{
      width: 5%;
      text-align: center;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
  td[colspan]{
    text-align: center;
    font-size: 19px;
    padding: 12px;
    @color: @filch;
    &:hover{
      cursor: pointer;
      background: @patronus-shade;
      color: @filch;
    }
  }
}
table.variants{
  button.remove{
    margin-right: 10px;
  }
  td{
    width: 60%;
    text-align: center;
    &:first-child{
      width: 5%;
      text-align: center;
    }
    &:nth-child(2){
      text-align: left;
      white-space: pre-wrap;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
}
table.questions{
  th:last-child{text-align: right}
  td{
    width: 1%;
    white-space: pre-wrap;
    &:first-child{
      width: 40%;
    }
    &:nth-child(2){
      width: 20%;
    }
    &:last-child{
      width: 11%;
      text-align: right;
    }
  }
}

.action-button{
  width: 100%;
  background: @patronus-shade;
  color: @filch;
  cursor: pointer;
  &:hover{
    color: @white;
    background: @patronus;
  }
}
.filter.theme{
  position: absolute;
}


.image-uploader{
  margin:0 !important;
  .row{
    margin-bottom: 15px;
    margin-right: -15px;
    margin-left: -15px;
    &:after{
      clear: both;
    }
    .img-preview{
      width: 25%;
      float:right;
    }
    .file-input{
      width: 70%;
      left: 10px;
      float: left;
      position: relative;
      z-index: 2;
      input{
        width: 70% !important;
        height: 35px;
        padding: 5px;
      }
    }
  }
  .upload-btn{
    padding: 7px;
    top: 0px;
    color: @white;
    padding: 8px;
    background: @patronus;
    &:hover{
      background: @patronus;
    }
  }
  .clean-btn{
    background: @akcio;
    color: @white;
    padding: 8px;
    border-radius: 0px 3px 3px 0;
    &:hover{
      background: @crimson-red;
    }
  }
  img{
    max-width: 70px;
    max-height: 70px;
    width: auto;
    height: auto;
  }
}
.image-holder{
  width: 100px;
  height: 100px;
  text-align: center;
  .remove{
    background: @black;
    opacity: 0.5;
    width: 15px;
    height: 15px;
    text-align: center;
    position: relative;
    z-index: 2;
    color: @white;
    float: right;
    font-size: 15px;
    cursor: pointer;
  }
  img{
    max-width: 100px;
    max-height: 100px;
    width: auto;
    height: auto;
    position: relative;
    cursor: pointer;
  }
}










#compile-modal{
  max-height: 450px;
  overflow: auto;
  h3{
    white-space: pre-wrap;
  }
}

.label(){
  font-size: 12px;
  color: @unimportant;
  span{
    color: @important;
  }
}
.width100{
  width: 100%;
}
span{
  &.ok{
    color: @mainTheme;
  }
  &.not-ok{
    color: @crimson-red;
  }
}

.multiselect-width(@wpercent){
  .multiselect-wrap {
    width: @wpercent;
    .multiselect {
      width: @wpercent;
    }
    input{
      width: @wpercent + 0.9;
    }
  }
  .knockout-autocomplete.menu{
    width: @wpercent - 4;
  }
}

#errors-modal{
  width: 300px;
  h3{
    display: inline-block;
  }
  h4{
    padding: 0 5px 5px;
    margin: 0 5px 5px;
  }
  .fa{
    padding-right:5px;
    font-size: 20px;
    color: @warning;
  }
  .button-holder{
    height: 30px;
    button{
      width: 70px;
      height: 30px;
      font-size: 15px;
      float: right;
    }
  }
}
#code-editor-modal{
  width: 800px;
  height: 450px;
  &#editor{
    position:relative;
  }
  .cancel, .save, #button{
    margin: 15px 0 0;
    color: @white;
    background: @mainTheme;
    cursor: pointer;
    border: 0;
    padding: 10px;
  }
  .cancel{
    background: @crimson-red;
    float: right;
  }
  .save{
    background: @buttonGreen;
    margin-right: 5px;
    float: right;
  }
  .ace_editor{
    width: 100%;
    height: 87%;
    border: 1px solid #e3e3e3;
  }
}

