a.active-link{
  font-size: 13px;
  color: @mainTheme;
  float: right;
  border-bottom: 0;
  &:hover{
    color: @black;
  }
}
button.move {
  width: 150px;
  &-section {
    width: 160px;
  }
}
button.add-section{
  width: 300px;
  background: @peterriver;
  &:hover{
    background: @belizehole;
  }
}
button{
  background: @buttonGreen;
  width: 50px;
  &:hover{
    background: @buttonHover;
  }
}


.tyle-btn{
  color: @neutral;
  width: 170px;
  background: @active;
  &-negative{
    border: 2px solid @active;
    background: @mainTheme;
  }
}

button.width200{
  width: 200px;
}

button.sq-small{
  width: 35px;
  height: 34px;
  margin-left: 5px;
}



.tooltip_templates {
  display: none;
}


.width60{
  width: 60%;
}

.manual{
  h1{
    text-align: center;
    padding: 30px;
    color: @mainTheme;
  }
  h3{
    padding: 10px 0 0 15px;
  }
  p{
    text-align: justify;
    padding: 10px 30px 20px 0;
  }
  .manual-image{
    max-height: 700px;
    max-width: 700px;
    width: auto;
    height: auto;
  }
}

.expanded-image{
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 50;
  top: 0;
  left: 0;
  position: fixed;
  display: none;
  img{
    display: block;
    margin: 0 auto;
  }
}

.LoadingImage{
  width: 100%;
  height: 100%;
  z-index: 1000;
  opacity: 0.3;
  position: absolute;
  display: none;
  img{
    display: block;
    margin: 0 auto;
    width: 100%;
    margin-top: -20%;
  }
}

.section-info{
  padding-top: 20px;
  padding-bottom: 10px;
  p{
    text-align: center;
  }
}

.section-name{
  padding: 20px;
  input{
    width: 85%;
    margin-left: 10px;
  }
  label{
    font-size: 18px;
  }
}

.section-buttons{
  padding: 10px;
  text-align: center;
  button{
    width: 100px;
  }
}

#sections-modal{
  width: 800px;
  table {
    td:first-child {
      width: 5%;
    }
    td:nth-child(2){
      width: 70%;
    }
    td:last-child {
      width: 15%;
      button.info {
        background: @peterriver;
      }
    }
  }
}


#cancel-modal{
  div{
    text-align: center;
    font-size: 18px;
    padding: 15px;
  }
}

.popup{
  &-delete{
    height: 80px;
    div:last-child{
      float: right;
      button:first-child{
        margin: 0 5px;
      }
    }
  }
  &-theme{
    div{
      margin: 10px 0;
    }
    input{
      width: 95%;
    }
    label{
      .label();
    }
    .popup-btn-group{
      float: right;
      margin: 5px 4px;
    }
  }
}
#add-theme-modal{
  width: 350px;
  height: 160px;
}
.plan-details {
  div {
    display: inline-table;
    margin: 10px 15px 25px;
    width: 11%;
    label{
      font-size: 12px;
      color: @unimportant;
      line-height: 40px;
    }
    &:first-child{
      width: 15%;
    }
    .plan-form{
      &-selected{
        font-size: 18px;
        color: @mainTheme;
      }
      font-size: 13px;
      color: @unimportant;
      line-height: 1.8;
      cursor: pointer;
    }
  }
  &:nth-child(odd) {
    background: @light-grey;
  }
  input{
    width: 70px;
  }
  select{
    width: 200px;
  }

}


