input[type=radio].custom-radio {
  vertical-align: top;
  width: 15px;
  height: 15px;
  margin: 0 3px 0 0;
}
input[type=radio].custom-radio + label {
  cursor: pointer;
  font-size: 16px;
  line-height: 30px;
}
input[type=radio].custom-radio:not(checked) {
  position: absolute;
  opacity: 0;
}
input[type=radio].custom-radio:not(checked) + label {
  position: relative;
  padding: 0 0 0 35px;
}
input[type=radio].custom-radio:not(checked) + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 19px;
  height: 19px;
  border: 1px solid #CDD1DA;
  border-radius: 50%;
  background: #FFF;
}
input[type=radio].custom-radio:not(checked) + label:after {
  content: '';
  position: absolute;
  top: 4px;
  left: 4px;
  width: 13px;
  height: 13px;
  border-radius: 50%;
  background: #5D8CAE;
  opacity: 0;
  transition: all .2s;
}
input[type=radio].custom-radio:checked + label:after {
  opacity: 1;
}
input[type=checkbox].custom-checkbox {
  vertical-align: top;
  width: 17px;
  height: 17px;
  margin: 0 3px 0 0;
}
input[type=checkbox].custom-checkbox + label {
  cursor: pointer;
  font-size: 16px;
  line-height: 30px;
}
input[type=checkbox].custom-checkbox:not(checked) {
  position: absolute;
  opacity: 0;
}
input[type=checkbox].custom-checkbox:not(checked) + label {
  position: relative;
  padding: 0 0 0 35px;
}
input[type=checkbox].custom-checkbox:not(checked) + label:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  border: 1px solid #CDD1DA;
  background: #fff;
}
input[type=checkbox].custom-checkbox:not(checked) + label:after {
  font-family: 'FontAwesome';
  font-size: 17px;
  content: '\f00c';
  color: #5D8CAE;
  position: absolute;
  left: 2px;
  top: -4px;
  opacity: 0;
  transition: all .2s;
}
input[type=checkbox].custom-checkbox:checked + label:after {
  opacity: 1;
}
